import { validate } from "validate.js";

// VALIDATION
class ValidateJS {
    constructor(form, inputmask=false, options) {
        this.$form = document.querySelector(form);
        this.options = options;
        this.inputmask = inputmask;

        this.$validationInputs = this.$form.querySelectorAll('.-validation');

        this.init();
    }

    init() {
        this.inputListener();
        this.submitListener();
        this.moments();

        if (this.inputmask) {
            this.inputMask();
        }
    }

    submitListener() {
        this.$form.addEventListener('keydown', (e)=> {
            if (e.code == 'NumpadEnter') {
                e.preventDefault();
            }
        })
        this.$form.addEventListener('submit', (e)=> {
            e.preventDefault();
            this.resetFormErrors();
            this.submitForm(this.$form);
            // this.$form.reset();
        })
    }
    submitForm() {
        let errors = validate(this.$form, this.options);
        
        if (!errors) {
            // this.success();
            this.resetFormSuccesses();
            // this.$form.reset();
        }
        this.showFormErrors(errors);
    }
    success() {
        alert('Success!');
    }
    showFormErrors(errors) {
        for (const key in errors) {
            if (Object.hasOwnProperty.call(errors, key)) {
                const value = errors[key];
                let el = this.$form.querySelector(`[name=${key}]`);
                this.showInputErrors(el, errors);
            }
        }
    }
    resetFormErrors() {
        for (let i = 0; i < this.$validationInputs.length; i++) {
            const element = this.$validationInputs[i];
            const errorInInput = element.closest('.-input-box').querySelector('.-error');

            if (errorInInput) {
                this.resetError(element); 
            }
        }
    }
    resetFormSuccesses() {
        for (let i = 0; i < this.$validationInputs.length; i++) {
            const element = this.$validationInputs[i];
            element.classList.remove('-valid');
        }
    }

    inputListener() {
        for (let i = 0; i < this.$validationInputs.length; i++) {
            const element = this.$validationInputs[i];

            element.addEventListener('input', ()=> {
                let errors = validate(this.$form, this.options);

                this.resetError(element);
                this.resetSuccess(element);
                this.showInputErrors(element, errors/*[element.name]*/);
            })
            element.addEventListener('blur', ()=> {
                let errors = validate(this.$form, this.options);

                this.resetError(element);
                this.resetSuccess(element);
                this.showInputErrors(element, errors/*[element.name]*/);
            })
        }
    }
    showInputErrors(el, errors) {
        if (errors && errors[el.name]) {
            if (!el.classList.contains('-invalid')) {
                el.classList.add('-invalid');
            }

            this.resetSuccess(el);

            errors[el.name].forEach(error => {
                this.addError(el, error);
            });

        } else {
            if (el.value.length) {
                this.inputSuccess(el);
            }
        }
    }
    addError(el, error) {
        let $errorsBlock = el.closest('.-input-box').querySelector('.-errors');
        $errorsBlock.insertAdjacentHTML('beforeend', 
            `<div class="-error">
                <div class="-error-img">
                    <img src="img/icons/info.svg">
                </div>
                <div class="-error-text">${error}</div>
            </div>`
        );               
    }
    resetError(el) {
        let $errorsBlock = el.closest('.-input-box').querySelector('.-errors');
        let $errorBlocks = $errorsBlock.querySelectorAll('.-error');

        if (el.classList.contains('-invalid')) {
            el.classList.remove('-invalid');
        }
        if ($errorBlocks.length) {
            $errorBlocks.forEach(errorBlock => {
                errorBlock.remove();
            });
        }
    }
    inputSuccess(el) {
        let $successesBlock = el.closest('.-input-box').querySelector('.-successes');
        if (!el.classList.contains('-valid')) {
            el.classList.add('-valid');
        }
        if ($successesBlock.querySelector('.-success')) {
            return
        }
        // $successesBlock.insertAdjacentHTML('beforeend', 
        //     `<div class="-success">OK</div>`
        // );
    }
    resetSuccess(el) {
        let $successesBlock = el.closest(".-input-box").querySelector('.-successes');
        let $successBlocks = $successesBlock.querySelectorAll('.-success');
        if (el.classList.contains('-valid')) {
            el.classList.remove('-valid');
        }
        if ($successBlocks) {
            $successBlocks.forEach(errorBlock => {
                errorBlock.remove();
            });
        }
    }
    moments() {
        validate.extend(validate.validators.datetime, {
            parse: function(value, options) {
              return +moment.utc(value, "DD MM YYYY");
            },
            format: function(value, options) {
              var format = options.dateOnly ? "DD MM YYYY" : "DD MM YYYY hh:mm:ss";
              return moment.utc(value,).format(format);
            }
        });
    }
    inputMask() {
        const maskDate = new Inputmask('99.99.9999', {jitMasking: true});
        maskDate.mask('#date');
        
        const maskPhone = new Inputmask('+375(99)[999-99-99]', {
            placeholder: ' ',
            jitMasking: true,
        });
        maskPhone.mask('#phone');
    }
}

const mainValidation = () => {
    // VALIDATION CONNECT
    let validationJS = new ValidateJS('#login-form', false, {
        email: {
            presence: {
                message: '^   ',
            },
            email: {
                message: "^    "
            }
        },
        password: {
            presence: {
                message: '^   ',
            },
            length: {
                minimum: 3,
                tooShort: "^  %{count} "
            }
        },
    });
}    

export { mainValidation }