const guestsBtn = document.getElementById("fv4-guests-btn");
const admirersBtn = document.getElementById("fv4-admirers-btn");
const invisibleBtn = document.getElementById("fv4-invisible-btn");
const statsBtn = document.getElementById("fv4-stats-btn");
const myAccountBtn = document.getElementById("fv4-my-account-btn");
const settingsBtn0 = document.getElementById("fv4-settings-btn0");
const settingsBtn = document.getElementById("fv4-settings-btn");
// const accountCloseBtn = document.querySelector('.acc__close-btn');
// const settingsCloseBtn = document.querySelector(".sett__close-btn");
const logoutBtn = document.getElementById('fv4-logout');
const changeAccBtn = document.getElementById('fv4-change-acc');

const questsBlock = document.querySelector('.fv4__contentBlock.guests-block');
const admirersBlock = document.querySelector('.fv4__contentBlock.admirers-block');
const invisibleBlock = document.querySelector('.fv4__contentBlock.invisible-block');
const statisticBlock = document.querySelector('.fv4__contentBlock.statistic-block');

const app = document.getElementById('fv4');
const appLogo = document.querySelector('.fv4__logo');
const title = document.querySelector('.fv4__body-title');
const btns = document.querySelectorAll('.fv4__menu-list-btn');
const contentBlocks = document.querySelectorAll('.fv4__contentBlock');
const demoBtns = document.querySelectorAll('.fv4-demo-btn');

const passToggle = document.querySelector('.login-form__passv-toggle');
const password = document.getElementById("password");

const appMenu = () => {

    appLogo.addEventListener('click', e => {
        activeBlock(questsBlock);
        activeBtn(guestsBtn);
        title.innerHTML = "GUESTS";
    });
    guestsBtn.addEventListener('click', e => {
        activeBlock(questsBlock);
        activeBtn(guestsBtn);
        title.innerHTML = "GUESTS";
    });
    admirersBtn.addEventListener('click', e => {
        activeBlock(admirersBlock);
        activeBtn(admirersBtn);
        title.innerHTML = "fans";

    });
    invisibleBtn.addEventListener('click', e => {
        activeBlock(invisibleBlock);
        activeBtn(invisibleBtn);
        title.innerHTML = "invisible mode";
    });
    statsBtn.addEventListener('click', e => {
        activeBlock(statisticBlock);
        activeBtn(statsBtn);
        title.innerHTML = "statistic";
    });
    myAccountBtn.addEventListener('click', e => {
        app.classList.contains('-settings-') && app.classList.remove('-settings-')
        app.classList.add('-account-');
        title.innerHTML = `ACCOUNT <img class="fv4__body-title-refresh" src="img/icons/refresh.svg"/>`;
    });
    settingsBtn.addEventListener('click', e => {
        app.classList.contains('-account-') && app.classList.remove('-account-')
        app.classList.add('-settings-');
        activeBtn(settingsBtn);
        title.innerHTML = "settings";
    });
    settingsBtn0.addEventListener('click', e => {
        app.classList.contains('-account-') && app.classList.remove('-account-')
        app.classList.add('-settings-');
        activeBtn(settingsBtn);
        title.innerHTML = "settings";
    });
    // accountCloseBtn.addEventListener('click', e => {
    //     app.classList.remove('-account-');
    // });
    // settingsCloseBtn.addEventListener('click', e => {
    //     app.classList.remove('-settings-');
    // });
    logoutBtn.addEventListener('click', e => {
        app.classList.remove('-account-');
        app.classList.add('-start-');
    });
    changeAccBtn.addEventListener('click', e => {
        app.classList.remove('-account-');
        app.classList.add('-start-');
    });

    demoBtns.forEach(btn => {
        btn.addEventListener('click', e => {
            app.classList.remove('-start-');
            activeBlock(questsBlock);
            activeBtn(guestsBtn);
            title.innerHTML = "ACCOUNT";
        })    
    });
    passToggle.addEventListener('click', e => {
        password.type == "password" ? (password.type = 'text') : (password.type = 'password');
    });



    function activeBlock(block) {
        contentBlocks.forEach(block => {
            block.classList.contains('-act-block-') && block.classList.remove('-act-block-');
        });
        app.classList.contains('-account-') && app.classList.remove('-account-')
        app.classList.contains('-settings-') && app.classList.remove('-settings-')
        block.classList.add("-act-block-");
    }
    function activeBtn(btn) {
        btns.forEach(btn => {
            btn.classList.contains('-act-btn-') && btn.classList.remove('-act-btn-');
        });
        btn.classList.add("-act-btn-");
    }
}




export default appMenu;